@import url(https://fonts.googleapis.com/css2?family=Zen+Kurenaido&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zen+Kurenaido&display=swap);

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.maincontainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 93vh;
    background-color: black;
    color: #ff00b3;
    font-size: xx-large;
}

.currimg {
    position: relative;
}

.currimg img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 7vh;
    height: 7vh;
    margin-top: 30px;
    margin-bottom: 20px;
    -webkit-animation: rotate 3s infinite;
            animation: rotate 3s infinite;
}

@-webkit-keyframes rotate {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    25% {
        -webkit-transform: rotateX(180deg);
                transform: rotateX(180deg);
    }
    50% {
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
    }
    75% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotate {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    25% {
        -webkit-transform: rotateX(180deg);
                transform: rotateX(180deg);
    }
    50% {
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
    }
    75% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@media only screen and (max-width: 600px) {
    .maincontainer {
        width: 100%;
        height: 95vh;
    }
    .currimg img {
        width: 7vh;
        height: 7vh;
    }
}
html {
    --desk-clr: 0 0 1px #ff00b3, 0 0 2px #ff00b3, 0 0 3px #ff00b3, 0 0 4px #ff00b3, 0 0 5px #ff00b3, 0 0 6px #ff00b3, 0 0 7px #ff00b3, 0 0 8px #ff00b3, 0 0 9px #ff00b3, 0 0 10px #ff00b3;
    --mobile-clr: 0 0 1px #ff00b3, 0 0 3px #ff00b3, 0 0 5px #ff00b3, 0 0 7px #ff00b3, 0 0 9px #ff00b3;
}

.navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000000;
    width: 100%;
    height: 7vh;
    min-height: 40px;
    border-bottom: solid 3.5px #FF0075;
}

.navtitle {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    font-size: x-large;
    font-family: 'Zen Kurenaido', sans-serif;
    font-weight: bolder;
    color: rgb(0, 0, 0);
    text-shadow: 0 0 1px #ff00b3, 0 0 2px #ff00b3, 0 0 3px #ff00b3, 0 0 4px #ff00b3, 0 0 5px #ff00b3, 0 0 6px #ff00b3, 0 0 7px #ff00b3, 0 0 8px #ff00b3, 0 0 9px #ff00b3, 0 0 10px #ff00b3;
    text-shadow: var(--desk-clr);
}

.navtitle img {
    width: 5vh;
    height: 5vh;
    margin-right: 5px;
}

@media only screen and (max-width: 600px) {
    .navbar {
        height: 50px;
        border-bottom: solid 2px #FF0075;
    }
    .navtitle {
        font-size: large;
        text-shadow: 0 0 1px #ff00b3, 0 0 3px #ff00b3, 0 0 5px #ff00b3, 0 0 7px #ff00b3, 0 0 9px #ff00b3;
        text-shadow: var(--mobile-clr);
    }
    .navtitle img {
        width: 35px;
        height: 35px;
    }
}
.convcontainer {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.inpcurrency {
    width: 20%;
    height: 60px;
    margin: 0;
    color: white;
    background-color: transparent;
    font-size: large;
    font-family: 'Zen Kurenaido', sans-serif;
    font-weight: bolder;
    border: 0;
    border-bottom: 2px solid #ff00b3;
    outline: 0;
    text-align: center;
}

.inpcurrency::-webkit-input-placeholder {
    font-weight: bolder;
    background-color: transparent;
    position: relative;
    top: 15px;
    text-align: center;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

.inpcurrency:-ms-input-placeholder {
    font-weight: bolder;
    background-color: transparent;
    position: relative;
    top: 15px;
    text-align: center;
    -ms-transition: all 0.5s;
    transition: all 0.5s;
}

.inpcurrency::placeholder {
    font-weight: bolder;
    background-color: transparent;
    position: relative;
    top: 15px;
    text-align: center;
    transition: all 0.5s;
}

.inpcurrency:focus {
    color: #ff00b3;
    font-weight: bolder;
}

.inpcurrency:focus::-webkit-input-placeholder {
    position: relative;
    top: -25px;
    font-size: x-small;
    color: #ff00b3;
    font-weight: bolder;
}

.inpcurrency:focus:-ms-input-placeholder {
    position: relative;
    top: -25px;
    font-size: x-small;
    color: #ff00b3;
    font-weight: bolder;
}

.inpcurrency:focus::placeholder {
    position: relative;
    top: -25px;
    font-size: x-small;
    color: #ff00b3;
    font-weight: bolder;
}

select {
    position: relative;
    top: 25%;
    width: 5%;
    height: 50%;
    text-align: center;
    color: white;
    margin-left: 2%;
    background-color: transparent !important;
    font-size: large;
    font-family: 'Zen Kurenaido', sans-serif;
    font-weight: bolder;
    border: 0;
    border-bottom: 2px solid #ff00b3;
    outline: 0;
    text-align: center;
}

select option {
    color: #ff00b3;
    background-color: black;
    font-size: large;
    font-family: 'Zen Kurenaido', sans-serif;
    font-weight: bolder;
    border: 0;
    outline: 0;
    text-align: center;
}

@media only screen and (max-width: 600px) {
    .inpcurrency {
        width: 50%;
    }
    select {
        width: 20%;
        height: 50%;
        margin-left: 5%;
    }
}
